import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { DataserviceService } from "app/dataservice.service";


@Component({
  selector: 'app-counter',
  templateUrl: './counter.component.html',
  styleUrls: ['./counter.component.css'],
  providers: [DataserviceService]
})
export class CounterComponent implements OnInit {
  public supervisionEntries: number;
  public teachingEntries: number;
  public projectEntries: number;
  public bibEntries: number;

  constructor(private dataservice: DataserviceService) { 
    this.dataservice.getNumberOfBibList().subscribe(response => {
      this.bibEntries = response;
    });

    this.dataservice.getNumberOfTheses().subscribe(response => {
      this.supervisionEntries = response;
    });

    this.dataservice.getNumberOfTeachings().subscribe(response => {
      this.teachingEntries = response;
    });

    this.dataservice.getNumberOfProjects().subscribe(response => {
      this.projectEntries = response;
    });
  }

  ngOnInit() {
    
  }

}



