import { Component, OnInit } from "@angular/core";
import { Http } from "@angular/http";
import { DataserviceService } from "app/dataservice.service";

import { Observable } from "rxjs";
import { catchError, tap, map } from "rxjs/operators";

declare var bibtexParse: any;
declare var $: any;
declare var jQuery: any;

@Component({
  selector: "app-publications",
  templateUrl: "./publications.component.html",
  styleUrls: ["./publications.component.css"],
  providers: [DataserviceService],
})
export class PublicationsComponent implements OnInit {
  bibList: Array<any>;
  searchText: string;

  constructor(
    private dataservice: DataserviceService,
    private httpClient: Http
  ) {}

  ngOnInit() {
    this.dataservice.getBibList().subscribe((response) => {
      response.forEach((element) => {
        element = this.consolidateTypes(element);
        element.bibtex = this.toBibtex(element);

        element.presentationAvailable =
          // Observable.of(
          this.fileExists(element.citationKey, "assets/presentations/", "pdf");
        // );
        element.paperAvailable =
          // Observable.of(
          this.fileExists(element.citationKey, "assets/papers/", "pdf");
        // );
        // console.log(element);
        this.loadThumbnail(element);
      });
      this.bibList = response;
    });

    // dirty hack since I do not know when the observables are resolved;
    // tooltip and popover needs to be called on the newly created items
    setTimeout(function () {
      $('[data-toggle="tooltip"]').tooltip();
      $('[data-toggle="popover"]').popover();
    }, 10000);
  }

  consolidateTypes(element) {
    element.entryType = element.entryType.toLowerCase();
    return element;
  }

  fileExists(
    citationKey: string,
    path: string,
    extension: string
  ): Observable<boolean> {
    citationKey = citationKey.replace("/", "").replace(":", "");

    return this.httpClient.get(path + citationKey + "." + extension).pipe(
      map((response) => {
        // console.log(
        //   "fileExists",
        //   citationKey,
        //   path,
        //   extension,
        //   path + citationKey + "." + extension
        // );
        return true;
      }),
      catchError((error) => {
        // console.log(
        //   "fileNOTExists",
        //   citationKey,
        //   path,
        //   extension,
        //   path + citationKey + "." + extension
        // );
        return Observable.of(false);
      })
    );
  }

  // fileExistsTest() {
  //   console.log("fileExistsTest");
  //   return true;
  // }

  // tslint:disable:forin
  toBibtex = function (json) {
    let out = "";
    out += "@" + json.entryType;
    out += "{";
    if (json.citationKey) {
      out += json.citationKey + ", " + "\n\t";
    }
    if (json.entry) {
      out += json.entry + "\n";
    }
    if (json.entryTags) {
      let tags = "";
      for (const jdx in json.entryTags) {
        if (tags.length !== 0) {
          tags += ", " + "\n\t";
        }
        tags += jdx + "= {" + json.entryTags[jdx] + "}";
      }
      out += tags;
    }
    out += "\n";
    out += "}\n\n";
    // console.log(out)
    return out;
  };

  // toBibtex(item) {
  //   console.log(item, bibtexParse.toBibtex(item));
  //   return bibtexParse.toBibtex(item);
  // }

  loadThumbnail(element) {
    // console.log(
    //   "location",
    //   element.citationKey,
    //   "assets/thumbs/" + element.citationKey + ".png"
    // );
    const filename =
      "assets/thumbs/" +
      element.citationKey.replaceAll("/", "").replaceAll(":", "") +
      ".png";
    this.httpClient.get(filename).subscribe(
      () => {
        // HANDLE file found
        // console.log("location found", element.citationKey);
        element.thumbnailUrl = filename;
        // "assets/thumbs/" + element.citationKey + ".png";
      },
      (err) => {
        // HANDLE file not found
        if (err.status === 404) {
          // console.log("location not found 404", location);
          return this.loadSecondFile(element);
        }
      }
    );
    return location;
  }

  private loadSecondFile(element) {
    const filename =
      "assets/thumbs/" +
      element.citationKey.replaceAll("/", "").replaceAll(":", "") +
      ".jpg";
    this.httpClient.get(filename).subscribe(
      () => {
        // console.log("location found jpg", element.citationKey);
        element.thumbnailUrl = filename;
        // "assets/thumbs/" + element.citationKey + ".jpg";
      },
      () => {
        element.thumbnailUrl = "assets/thumbs/noimage.png";
        // HANDLE file not found
      }
    );
  }
}



