import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "latex",
})
export class LatexPipe implements PipeTransform {
  //    (hold down the Alt key and type 0196)
  //  (Alt 0228)
  //  (Alt 0214)
  //  (Alt 0246)
  //  (Alt 0220)
  //  (Alt 0252)
  //  (Alt 0223)
  transform(value: string, args?: any): string {
    // let regex = new RegExp(find, "gm");
    if (value === undefined) {
      return "";
    }

    value = value.replace(new RegExp('{\\"{A}}|\\"{A}', "gm"), "");
    value = value.replace(new RegExp('{\\"{a}|\\"{a}||\\"a', "gm"), "");

    value = value.replace(new RegExp('\\"{O}|{\\"{O}}', "gm"), "");
    value = value.replace(new RegExp('{\\"{o}}|\\"{o}||\\"o', "gm"), "");

    value = value.replace(new RegExp('\\"{U}|{\\"{U}}', "gm"), "");
    value = value.replace(new RegExp('{\\"{u}}|\\"{u}|\\"u', "gm"), "");

    value = value.replace(new RegExp("{\\'e}|\\'e|{'e}", "gm"), "");

    value = value.replace(new RegExp("&", "gm"), "&");

    value = value.replace(new RegExp("{|}", "gm"), "");

    value = value.replace(new RegExp("{-}", "gm"), "-");

    // return value.replace(new RegExp("\\", "gm"), "");
    return value;
  }
}



